html {
  height:100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-logo {
  height: 85vmin;
  pointer-events: none;
}

.App-header {
  margin-top:24px;
  background-color: #2F4F4F;
  min-height: 40vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 50px;
  
}
.App-footer {
  background-color: #2C3531;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
  width:100%;
  position:fixed;
  background-color:	#2C3531;
  height: 2.5rem;
  bottom: 0px;
  left: 0px;
  
  
}

.App-link {
  color: #61dafb;
}

.carousel {
  width: 50rem; 
  margin: auto;
}

.heart-video {
  width: 50rem;
  height: 25rem; 
  margin: auto;
}
.spacing {
  -webkit-flex: 1 1;
          flex: 1 1;
  /* takes up all space in nav */
}
.toolbar{
  width:100%;
  position:fixed;
  background-color:	#2C3531;
  height: 2.5rem;
  top: 0px;
  left: 0px;
}
.toolbar__navigation{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
          align-items:center;
  height:100%;
}

.toolbar__logo {
  margin-left: 1rem;
  width: 100%;
  min-width: 400px;
}

.toolbar__logo a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
  width: 100%;
}
.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
}
.toolbar_navigation-items a {
  color:white;
  padding: 0 0.4rem;
}
.toolbar__logo a:hover,
.toolbar__logo a:hover,
.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #FFCB9A;
}
.toggle-button {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height:2rem;
  width:2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  /* padding:0; */
  box-sizing:border-box;
}
.toggle-button:focus {
  outline: none;
}

.toggle-button_line {
  width: 30px;
  height: 2px;
  background: white;
}
.contact-menu {
  height: 100%;
  background: teal;
  box-shadow: 2px 0px 5px rgba(0,0,0,0.2);
  position:fixed;
  left: right;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  color: white;
}

.contact-menu ul {
  height:100%;
  font-family: 'Courier New', Courier, monospace;
  font-size: 100%;
  display:  -webkit-flex;
  display:  flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  
}

.contact-menu li {
  margin: 0.5rem 0;
  list-style: none;
}

.contact-menu a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.contact-menu a:hover, 
.contact-menu a:active {
  color:#FFCB9A;
}
