.self-port {
  height: 40vmin;
}

.backdrop {
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  z-index: 100;

}
.aboutmetext {
  font-size: 1.25rem;
  /* color:red; */
  width:50%;
  /* min-width: 50px; */
  margin-left: 25%;
  min-height: 50px;
  max-width: 1500px;
  max-height: 1000px;
  word-wrap: break-word;
  text-align: justify;  
